@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Poppins',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.topnav{border-bottom:#FF4655 solid 3px;background-color:#0f1923;top:0;height:80px;width:100%;transition:top 0.3s;display:flex;transition:opacity ease .3s;position:fixed}.navList{margin:0;opacity:1;margin-left:18px;width:100%}.navListItem{list-style:none;display:inline-flex;position:relative;height:80px}@keyframes fadeInAnimation{0%{opacity:0}100%{opacity:1}}

.menubutton{display:flex;margin:auto;padding:0;flex-direction:column;justify-content:center;color:#f9f9f9;font-size:13px;font-weight:600;line-height:120%;letter-spacing:.08em;overflow:hidden;text-transform:uppercase;position:relative;background-color:#0f1923}.menubutton :hover{background:rgba(128,128,128,0.3);border-radius:6.4px}.buttontext{position:relative;display:block;padding:7.5px 16px;text-align:center;font:inherit;margin-right:4px;z-index:1;text-align:center;cursor:pointer;color:white;background-color:transparent}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}a{text-decoration:none}.hero-container{margin-top:80px;display:flex;justify-items:center;background-color:#0f1923;cursor:default;height:100vh;width:100%;line-height:1.3}.hero-content{margin:0 25% 0 25%;padding:4rem;width:100%;color:#94eca9;font-family:"Poppins",sans-serif}.intro-text{line-height:1.5;font-size:1rem;max-width:600px}.name-text{margin:0 !important;font-size:4rem}

.carousel-container{white-space:nowrap;transition:transform 0.3s;box-shadow:0 5px 15px rgba(0,0,0,0.05);background:#f1f1f1;display:flex;flex-wrap:nowrap;overflow:hidden}.carousel-item{display:inline-flex;align-items:center;justify-content:center;height:490px;background-color:#fff}.indicators{margin-top:20px;background:none}.indicators .indicator-buttons{border:none;margin:none;background:none}.button-arrow{background:none;border:none;cursor:pointer;margin-top:20px;color:red}

